.Hangman {
  color: black;
  text-align: center;
  zoom: 1.3;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  margin: auto;
}

.Hangman img {
  border: 10px solid white;
  -webkit-box-shadow: 0 10px 30px rgba(0, 0, 0, 0.27),
    0 0 40px rgba(0, 0, 0, 0.06) inset;
          box-shadow: 0 10px 30px rgba(0, 0, 0, 0.27),
    0 0 40px rgba(0, 0, 0, 0.06) inset;
}

.Hangman h1 {
  width: 100%;
  font-size: 4rem;
  font-weight: 100;
  margin-bottom: 0.1em;
  margin-top: 0;
}

.Hangman-left,.Hangman-right{
  width: 50%;

}
.Hangman-left{
  margin: auto;
}
.Hangman-right{
margin: auto;
}

.Hangman-word {
  letter-spacing: 0.5em;
  margin: 0.4em 0 0.2em 0;
  font-size: 2rem;
}

.Hangman-btns {
  text-align: center;
  display: inline-block;
  width: 250px;
  margin-top: 0;
}

.Hangman button {
  border: none;
  font-family: inherit;
  font-size: 1rem;
  cursor: pointer;
  width: 30px;
  margin: 5px 2px 2px 2px;
  letter-spacing: 1px;
  outline: none;
  background: black;
  color: #fff;
  -webkit-box-shadow: 0 6px darkgray;
          box-shadow: 0 6px darkgray;
  border-radius: 5px;
  padding-top: 4px;
}

.Hangman button:hover {
  background-color: black;
}

.Hangman button:disabled {
  background: #bdbdbd;
  color: #eeeeee;
  -webkit-box-shadow: 0 6px black;
          box-shadow: 0 6px black;
}

.Hangman-counters{
  display: -ms-flexbox;
  display: flex;
}
.Hangman-counters p{
  margin: auto;
  margin-top: 25px;
}

#res-btn{
  width: auto;
  display: block;
  margin: auto;
  margin-bottom: 25px;
  background-color: tomato;
  -webkit-box-shadow: 0 6px #7c2c2c;
          box-shadow: 0 6px #7c2c2c;

}


 @media only screen and (max-width: 768px) {
  .Hangman-left{
    height: 100px;
  }
  .Hangman-left img{
    -o-object-fit: contain;
       object-fit: contain;
    width: 100%;
    height: 100%;
  }
  .Hangman h1{
  font-size: 2rem;
  }
  
  .Hangman-left,.Hangman-right{
    width: 100%;
  
  }

 }


